import React, { useState } from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  useNavigate,
} from "react-router-dom";

import StartScreen from "./components/StartScreen";
import MainScreen from "./components/MainScreen";
import Quizy from "./components/Quizy";
import Mapa from "./components/Mapa";
import Statystyki from "./components/Statystyki";
import UserProfile from "./components/UserProfile";  

function DropDownMenu({ onShowProfile }) {
  const [open, setOpen] = useState(false);
  const navigate = useNavigate();

  const goTo = (path) => {
    setOpen(false);
    navigate(path);
  };

  return (
    <div style={{ position: "relative", marginBottom: 20 }}>
      <button onClick={() => setOpen(!open)}>Menu</button>
      {open && (
        <div
          style={{
            position: "absolute",
            top: "100%",
            left: 0,
            background: "#eee",
            border: "1px solid #ccc",
          }}
        >
          <div onClick={() => goTo("/")} style={itemStyle}>
            Start
          </div>
          <div onClick={() => goTo("/main")} style={itemStyle}>
            Main Screen
          </div>
          <div onClick={() => goTo("/quizy")} style={itemStyle}>
            Quizy
          </div>
          <div onClick={() => goTo("/mapa")} style={itemStyle}>
            Mapa
          </div>
          <div onClick={() => goTo("/statystyki")} style={itemStyle}>
            Statystyki
          </div>
          <div onClick={() => { setOpen(false); onShowProfile(); }} style={itemStyle}>
            Profil uytkownika
          </div>
        </div>
      )}
    </div>
  );
}

const itemStyle = {
  padding: 8,
  cursor: "pointer",
  borderBottom: "1px solid #ddd",
};

function AppLayout({ favorites, addFavorite, removeFavorite }) {
  const [showProfile, setShowProfile] = useState(false);

  return (
    <>
      <DropDownMenu onShowProfile={() => setShowProfile(true)} />

      <Routes>
        <Route
          path="/"
          element={<StartScreen />}
        />
        <Route
          path="/main"
          element={
            <MainScreen
              favorites={favorites}
              addFavorite={addFavorite}
              removeFavorite={removeFavorite}
            />
          }
        />
        <Route path="/quizy" element={<Quizy />} />
        <Route path="/mapa" element={<Mapa />} />
        <Route path="/statystyki" element={<Statystyki />} />
      </Routes>

      {showProfile && (
        <UserProfile
          username="jan.kowalski"
          favorites={favorites}
          onRemoveFavorite={removeFavorite}
          onDeleteAccount={() => {
            alert("Konto usunite");
            setShowProfile(false);
          }}
          onClose={() => setShowProfile(false)}
        />
      )}
    </>
  );
}

export default function App() {
  const [favorites, setFavorites] = useState([]);

  const addFavorite = (animal) => {
    setFavorites((prev) => {
      if (prev.find((a) => a.id === animal.id)) return prev; 
      return [...prev, animal];
    });
    alert(`Dodano ${animal.name} do ulubionych!`);
  };

  const removeFavorite = (id) => {
    setFavorites((prev) => prev.filter((a) => a.id !== id));
  };

  return (
    <Router>
      <AppLayout
        favorites={favorites}
        addFavorite={addFavorite}
        removeFavorite={removeFavorite}
      />
    </Router>
  );
}
