import React, { useState } from "react";

export default function UserProfile({
  username,
  favorites = [],
  onRemoveFavorite,
  onDeleteAccount,
  onClose, // nowy props do zamykania okna
}) {
  const [showSettings, setShowSettings] = useState(false);
  const [newPassword, setNewPassword] = useState("");

  const handlePasswordChange = () => {
    if (newPassword.length < 6) {
      alert("Haso musi mie co najmniej 6 znakw.");
      return;
    }
    alert("Haso zostao zmienione!");
    setNewPassword("");
  };

  const handleDeleteAccount = () => {
    if (
      window.confirm(
        "Czy na pewno chcesz usun konto? Tej operacji nie mona cofn."
      )
    ) {
      alert("Konto usunite.");
      if (onDeleteAccount) {
        onDeleteAccount();
      }
      onClose(); // zamykamy modal po usuniciu konta
    }
  };

  return (
    <div
      style={{
        position: "fixed",
        top: 0, left: 0, right: 0, bottom: 0,
        backgroundColor: "rgba(0,0,0,0.5)",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        zIndex: 1000,
      }}
      onClick={onClose} 
    >
      <div
        style={{
          background: "#fff",
          padding: "2rem",
          borderRadius: "12px",
          boxShadow: "0 0 20px rgba(0,0,0,0.25)",
          width: "90%",
          maxWidth: "500px",
          fontFamily: "'Segoe UI', sans-serif",
          position: "relative",
        }}
        onClick={(e) => e.stopPropagation()} 
      >
        <button
          onClick={onClose}
          style={{
            position: "absolute",
            top: "12px",
            right: "12px",
            background: "transparent",
            border: "none",
            fontSize: "1.5rem",
            cursor: "pointer",
            color: "#666",
          }}
          aria-label="Zamknij profil"
        >
          &times;
        </button>

        <h2 style={{ fontSize: "1.8rem", marginBottom: "1rem" }}>
          Profil uytkownika
        </h2>
        <p style={{ fontSize: "1rem", color: "#555", marginBottom: "1.5rem" }}>
           {username}
        </p>

        <div style={{ marginBottom: "2rem" }}>
          <h3 style={{ fontSize: "1.2rem", marginBottom: "0.8rem" }}>
             Ulubione zwierzta
          </h3>
          {favorites.length === 0 ? (
            <p style={{ color: "#999" }}>Brak ulubionych zwierzt.</p>
          ) : (
            <ul style={{ listStyle: "none", padding: 0 }}>
              {favorites.map((animal) => (
                <li
                  key={animal.id}
                  style={{
                    display: "flex",
                    justifyContent: "space-between",
                    padding: "0.5rem 0",
                    borderBottom: "1px solid #eee",
                  }}
                >
                  <span>{animal.name}</span>
                  <button
                    onClick={() => onRemoveFavorite(animal.id)}
                    style={{
                      background: "#ef5350",
                      color: "#fff",
                      border: "none",
                      padding: "0.3rem 0.6rem",
                      borderRadius: "4px",
                      cursor: "pointer",
                    }}
                    aria-label={`Usu ${animal.name} z ulubionych`}
                  >
                    Usu
                  </button>
                </li>
              ))}
            </ul>
          )}
        </div>

        <div>
          <h3 style={{ fontSize: "1.2rem", marginBottom: "0.8rem" }}>
             Ustawienia konta
          </h3>
          <button
            style={{
              padding: "0.5rem 1rem",
              background: "#90caf9",
              border: "none",
              borderRadius: "5px",
              cursor: "pointer",
              marginBottom: "1rem",
            }}
            onClick={() => setShowSettings(!showSettings)}
            aria-expanded={showSettings}
          >
            {showSettings ? "Ukryj ustawienia" : "Poka ustawienia"}
          </button>

          {showSettings && (
            <div
              style={{
                marginTop: "1rem",
                background: "#f1f1f1",
                padding: "1rem",
                borderRadius: "8px",
              }}
            >
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  gap: "0.5rem",
                  marginBottom: "1rem",
                }}
              >
                <label htmlFor="new-password">Nowe haso:</label>
                <input
                  id="new-password"
                  type="password"
                  value={newPassword}
                  onChange={(e) => setNewPassword(e.target.value)}
                  style={{
                    padding: "0.5rem",
                    border: "1px solid #ccc",
                    borderRadius: "4px",
                  }}
                />
                <button
                  onClick={handlePasswordChange}
                  style={{
                    background: "#4caf50",
                    color: "#fff",
                    border: "none",
                    padding: "0.5rem",
                    borderRadius: "4px",
                    cursor: "pointer",
                  }}
                >
                  Zmie haso
                </button>
              </div>
              <hr />
              <button
                onClick={handleDeleteAccount}
                style={{
                  background: "#d32f2f",
                  color: "#fff",
                  border: "none",
                  padding: "0.5rem 1rem",
                  borderRadius: "4px",
                  cursor: "pointer",
                  marginTop: "1rem",
                }}
              >
                Usu konto
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
