import React from "react";
import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";
import L from "leaflet";
import "leaflet/dist/leaflet.css";
import animals from "../data/animalsData";

const mapStyle = {
  height: "100vh",
  width: "100%",
  border: "2px solid #ccc",
  borderRadius: "10px",
};

export default function Mapa() {
  return (
    <div style={{ padding: "20px" }}>
      <h2 style={{ textAlign: "center" }}> Mapa wystpowania zwierzt</h2>
      <MapContainer center={[20, 0]} zoom={2} style={mapStyle}>
        <TileLayer
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        {animals.map((animal, index) => (
          <Marker
            key={index}
            position={[animal.lat, animal.lng]}
            icon={L.divIcon({
              className: "custom-icon",
              html: `<div style="font-size: 24px;">${animal.icon}</div>`
            })}
          >
            <Popup>
              <strong>{animal.name}</strong>
              <br />
              Miejsce: {animal.region}
            </Popup>
          </Marker>
        ))}
      </MapContainer>
    </div>
  );
}
