import React, { useEffect, useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import Lottie from "lottie-react";
import explosionAnim from "../animations/explosion.json";
import MainScreen from "./MainScreen";
import "../components/StartScreen.css";

const animals = [
  { name: "Tiger", emoji: "", width: 120 },
  { name: "Elephant", emoji: "", width: 160 },
  { name: "Alpaca", emoji: "", width: 120 },
];

export default function App() {
  const [phase, setPhase] = useState("run");
  const [shake, setShake] = useState(false);
  const [flash, setFlash] = useState(false);
  const [started, setStarted] = useState(false);
  const [animateOut, setAnimateOut] = useState(false);

  useEffect(() => {
    if (started) return;

    const showExplosion = setTimeout(() => setPhase("explosion"), 5000);
    const triggerEffects = setTimeout(() => {
      setShake(true);
      setFlash(true);
      setTimeout(() => setShake(false), 500);
      setTimeout(() => setFlash(false), 300);
    }, 5500);
    const showTitle = setTimeout(() => setPhase("title"), 6000);

    return () => {
      clearTimeout(showExplosion);
      clearTimeout(triggerEffects);
      clearTimeout(showTitle);
    };
  }, [started]);

  useEffect(() => {
    const handleKey = () => {
      if (!started && !animateOut) setAnimateOut(true);
    };
    window.addEventListener("keydown", handleKey);
    return () => window.removeEventListener("keydown", handleKey);
  }, [started, animateOut]);

  useEffect(() => {
    if (animateOut) {
      const timer = setTimeout(() => setStarted(true), 1200);
      return () => clearTimeout(timer);
    }
  }, [animateOut]);

  if (started) return <MainScreen />;

  return (
    <motion.div
      className={`container ${shake ? "shake" : ""}`}
      initial={{ y: 0 }}
      animate={animateOut ? { y: "-100vh" } : { y: 0 }}
      transition={{ duration: 1.2, ease: "easeInOut" }}
    >
      <div className="forest-background" />
      {flash && <div className="flash" />}

      {[...Array(10)].map((_, i) => (
        <div
          key={i}
          className="leaf"
          style={{ left: `${i * 10}%`, animationDelay: `${i * 0.5}s` }}
        />
      ))}

      <AnimatePresence>
        {phase === "run" && (
          <div className="animals-row">
            {animals.map((animal, i) => (
              <motion.span
                key={animal.name}
                className="animal-emoji"
                style={{ fontSize: animal.width }}
                initial={{ x: "110vw", y: 0 }}
                animate={{ x: "-150vw", y: [0, -50, 0, -35, 0] }}
                exit={{ opacity: 0 }}
                transition={{
                  duration: 5,
                  delay: i * 1.2,
                  ease: "easeInOut",
                }}
                role="img"
                aria-label={animal.name}
              >
                {animal.emoji}
              </motion.span>
            ))}
          </div>
        )}
      </AnimatePresence>

      <AnimatePresence>
        {phase === "explosion" && (
          <motion.div
            className="explosion"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 2 }}
          >
            <Lottie animationData={explosionAnim} loop={false} />
          </motion.div>
        )}
      </AnimatePresence>

      {phase === "title" && (
        <motion.h1
          className="title"
          initial={{ opacity: 0, y: 100 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 1.5, ease: "easeOut" }}
        >
          ENCYKLOPEDIA ZWIERZT
        </motion.h1>
      )}
    </motion.div>
  );
}
