import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

export default function DropDownMenu() {
  const [open, setOpen] = useState(false);
  const navigate = useNavigate();

  const goTo = (path) => {
    setOpen(false);
    navigate(path);
  };

  return (
    <div style={{ position: "relative", marginBottom: 20 }}>
      <button
        onClick={() => setOpen(!open)}
        style={{
          padding: "10px 15px",
          backgroundColor: "#007acc",
          color: "white",
          border: "none",
          borderRadius: 4,
          cursor: "pointer",
        }}
      >
        Menu
      </button>

      {open && (
        <div
          style={{
            position: "absolute",
            top: "100%",
            left: 0,
            background: "white",
            border: "1px solid #ccc",
            boxShadow: "0 2px 5px rgba(0,0,0,0.1)",
            borderRadius: 4,
            zIndex: 100,
          }}
        >
          <div onClick={() => goTo("/")} style={menuItemStyle}>Start</div>
          <div onClick={() => goTo("/main")} style={menuItemStyle}>Main</div>
          <div onClick={() => goTo("/quizy")} style={menuItemStyle}>Quizy</div>
          <div onClick={() => goTo("/mapa")} style={menuItemStyle}>Mapa</div>
          <div onClick={() => goTo("/statystyki")} style={menuItemStyle}>Statystyki</div>
        </div>
      )}
    </div>
  );
}

const menuItemStyle = {
  padding: "10px 15px",
  cursor: "pointer",
  borderBottom: "1px solid #eee",
  backgroundColor: "#fff",
  transition: "background 0.2s",
};
