import React, { useState } from "react";

export default function AuthModal({ onClose, onLogin }) {
  const [mode, setMode] = useState("login");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [role, setRole] = useState("user"); 
  
  // Pobierz uytkownikw z localStorage
  const getAllUsers = () => {
    const users = localStorage.getItem("users");
    return users ? JSON.parse(users) : [];
  };

  // Zapisz uytkownikw w localStorage
  const saveAllUsers = (users) => {
    localStorage.setItem("users", JSON.stringify(users));
  };

  const handleRegister = () => {
    if (!username.trim() || !password.trim()) {
      alert("Wypenij wszystkie pola");
      return;
    }

    const users = getAllUsers();

    if (users.some((u) => u.username === username)) {
      alert("Uytkownik o takiej nazwie ju istnieje");
      return;
    }

    const newUser = { username, password, role };
    users.push(newUser);
    saveAllUsers(users);

    alert("Zarejestrowano pomylnie. Moesz si teraz zalogowa.");
    setMode("login");
    setUsername("");
    setPassword("");
  };

  const handleLogin = () => {
    if (!username.trim() || !password.trim()) {
      alert("Wypenij wszystkie pola");
      return;
    }

    const users = getAllUsers();
    const foundUser = users.find((u) => u.username === username && u.password === password);

    if (!foundUser) {
      alert("Nieprawidowa nazwa uytkownika lub haso");
      return;
    }

    onLogin({ username: foundUser.username, role: foundUser.role });
  };

  return (
    <div
      style={{
        position: "fixed",
        top: 50,
        left: "50%",
        transform: "translateX(-50%)",
        backgroundColor: "white",
        border: "1px solid #ddd",
        padding: 20,
        borderRadius: 8,
        width: 300,
        zIndex: 2000,
        boxShadow: "0 2px 10px rgba(0,0,0,0.2)",
      }}
    >
      <h2>{mode === "login" ? "Logowanie" : "Rejestracja"}</h2>

      <input
        type="text"
        placeholder="Nazwa uytkownika"
        value={username}
        onChange={(e) => setUsername(e.target.value)}
        style={{ width: "100%", padding: 8, marginBottom: 10 }}
      />
      <input
        type="password"
        placeholder="Haso"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
        style={{ width: "100%", padding: 8, marginBottom: 10 }}
      />

      {mode === "register" && (
        <select
          value={role}
          onChange={(e) => setRole(e.target.value)}
          style={{ width: "100%", padding: 8, marginBottom: 10 }}
        >
          <option value="user">Uytkownik</option>
          <option value="admin">Administrator</option>
        </select>
      )}

      {mode === "login" ? (
        <>
          <button
            onClick={handleLogin}
            style={{
              width: "100%",
              padding: 10,
              backgroundColor: "#007acc",
              color: "white",
              border: "none",
              borderRadius: 4,
              cursor: "pointer",
            }}
          >
            Zaloguj si
          </button>
          <p style={{ marginTop: 10 }}>
            Nie masz konta?{" "}
            <button
              onClick={() => {
                setMode("register");
                setUsername("");
                setPassword("");
              }}
              style={{ color: "blue", textDecoration: "underline", cursor: "pointer", background: "none", border: "none", padding: 0 }}
            >
              Zarejestruj si
            </button>
          </p>
        </>
      ) : (
        <>
          <button
            onClick={handleRegister}
            style={{
              width: "100%",
              padding: 10,
              backgroundColor: "green",
              color: "white",
              border: "none",
              borderRadius: 4,
              cursor: "pointer",
            }}
          >
            Zarejestruj si
          </button>
          <p style={{ marginTop: 10 }}>
            Masz ju konto?{" "}
            <button
              onClick={() => {
                setMode("login");
                setUsername("");
                setPassword("");
              }}
              style={{ color: "blue", textDecoration: "underline", cursor: "pointer", background: "none", border: "none", padding: 0 }}
            >
              Zaloguj si
            </button>
          </p>
        </>
      )}

      <button
        onClick={onClose}
        style={{
          marginTop: 10,
          width: "100%",
          padding: 10,
          backgroundColor: "#ccc",
          border: "none",
          borderRadius: 4,
          cursor: "pointer",
        }}
      >
        Zamknij
      </button>
    </div>
  );
}
