import React, { useState, useEffect } from "react";
import AuthModal from "./AuthModal";
import UserProfile from "./UserProfile";
import DropDownMenu from "./DropDownMenu"; 

export default function MainScreen() {

  const [user, setUser] = useState(null);
  const [showAuthModal, setShowAuthModal] = useState(false);
  const [showProfile, setShowProfile] = useState(false);
  const [query, setQuery] = useState("");
  const [searchResults, setSearchResults] = useState([]);
  const [selectedAnimal, setSelectedAnimal] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // Menu dropdown
  const [showDropdown, setShowDropdown] = useState(false);

  // Opinie i oceny
  const [reviewText, setReviewText] = useState("");
  const [ratings, setRatings] = useState({ sladkosc: 3, ciekawosc: 3, groznosc: 3 });
  const [reviews, setReviews] = useState([]);

  // Pomocnicze funkcje localStorage
  const getAllUsers = () => {
    const users = localStorage.getItem("users");
    return users ? JSON.parse(users) : [];
  };

  const saveAllUsers = (users) => {
    localStorage.setItem("users", JSON.stringify(users));
  };

  

  // Usuwanie konta
  const deleteAccount = (usernameToDelete) => {
    if (!user) return alert("Nie jeste zalogowany");

    if (user.role !== "admin" && user.username !== usernameToDelete) {
      return alert("Nie masz uprawnie do usunicia tego konta.");
    }

    const users = getAllUsers();
    const filteredUsers = users.filter((u) => u.username !== usernameToDelete);
    saveAllUsers(filteredUsers);

    localStorage.removeItem(`favorites_${usernameToDelete}`);

    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (key && key.startsWith("reviews_")) {
        const revs = JSON.parse(localStorage.getItem(key)) || [];
        const filteredRevs = revs.filter((r) => r.user !== usernameToDelete);
        localStorage.setItem(key, JSON.stringify(filteredRevs));
      }
    }

    alert(`Konto ${usernameToDelete} zostao usunite.`);

    if (usernameToDelete === user.username) {
      setUser(null);
      setShowProfile(false);
    }
  };

  // Wyszukiwanie zwierzt
  const searchAnimals = async (searchTerm) => {
    if (!searchTerm.trim()) {
      setSearchResults([]);
      return;
    }
    setLoading(true);
    setError(null);
    try {
      const response = await fetch(
        `https://pl.wikipedia.org/w/api.php?action=query&list=search&srsearch=${encodeURIComponent(
          searchTerm
        )}&format=json&origin=*`
      );
      const data = await response.json();
      setSearchResults(data.query.search);
    } catch {
      setError("Bd podczas wyszukiwania");
    } finally {
      setLoading(false);
    }
  };

  // Szczegy zwierzcia
  const fetchAnimalDetails = async (title) => {
    setLoading(true);
    setError(null);
    try {
      const response = await fetch(
        `https://pl.wikipedia.org/api/rest_v1/page/summary/${encodeURIComponent(title)}`
      );
      const data = await response.json();
      setSelectedAnimal(data);
    } catch {
      setError("Bd podczas pobierania szczegw");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (!selectedAnimal) return;
    const savedReviews = localStorage.getItem(`reviews_${selectedAnimal.title}`);
    setReviews(savedReviews ? JSON.parse(savedReviews) : []);
    setReviewText("");
    setRatings({ sladkosc: 3, ciekawosc: 3, groznosc: 3 });
  }, [selectedAnimal]);

  // Dodawanie opinii
  const addReview = () => {
    if (!user) {
      alert("Musisz si zalogowa, aby doda opini.");
      return;
    }
    if (!reviewText.trim()) {
      alert("Opinia nie moe by pusta.");
      return;
    }
    const newReview = {
      user: user.username,
      text: reviewText,
      ratings,
      date: new Date().toISOString(),
    };
    const updatedReviews = [...reviews, newReview];
    setReviews(updatedReviews);
    localStorage.setItem(`reviews_${selectedAnimal.title}`, JSON.stringify(updatedReviews));
    setReviewText("");
    setRatings({ sladkosc: 3, ciekawosc: 3, groznosc: 3 });
  };

  // Dodaj do ulubionych
  const addFavorite = (animalName) => {
    if (!user) {
      alert("Musisz si zalogowa, aby dodawa ulubione.");
      return;
    }
    const storageKey = `favorites_${user.username}`;
    const storedFavs = localStorage.getItem(storageKey);
    let favs = storedFavs ? JSON.parse(storedFavs) : [];

    if (favs.includes(animalName)) {
      alert(`${animalName} jest ju w Twoich ulubionych.`);
      return;
    }

    favs.push(animalName);
    localStorage.setItem(storageKey, JSON.stringify(favs));
    alert(`${animalName} zosta dodany do ulubionych!`);
  };

  // Obsuga wyboru w menu dropdown
  const handleMenuSelect = (option) => {
    setShowDropdown(false);
    alert(`Wybrano: ${option}`);
  };

return (
  <div
    style={{
      backgroundImage: `url(${process.env.PUBLIC_URL + '/las.jpg'})`,
      backgroundSize: 'cover',
      backgroundPosition: 'center',
      backgroundRepeat: 'no-repeat',
      minHeight: '100vh',
      padding: 20,
      position: 'relative',
      color: 'black'
    }}
  >
    {/* HEADER z przyciskiem Menu po lewej i reszt po prawej */}
    <header
      style={{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        marginBottom: 20,
        position: "relative",
      }}
    >
      {/* Przycisk Menu po lewej */}
      <DropDownMenu />

      {showDropdown && <DropDownMenu onSelect={handleMenuSelect} />}
      {/* Reszta headera po prawej */}
      <div>
        {user ? (
          <>
            <span style={{ marginRight: 15 }}>Witaj, {user.username}!</span>
            <button onClick={() => setShowProfile(true)} style={{ marginRight: 10 }}>
              Profil
            </button>
            <button
              onClick={() => {
                setUser(null);
                setShowProfile(false);
                alert("Wylogowano");
              }}
            >
              Wyloguj si
            </button>
          </>
        ) : (
          <button onClick={() => setShowAuthModal(true)}>Rejestracja / Logowanie</button>
        )}
      </div>
    </header>

      {/* Wyszukiwarka */}
      <main style={{ maxWidth: 600, margin: "0 auto" }}>
        <h1>Wyszukiwarka zwierzt</h1>
        <input
          type="text"
          placeholder="Wpisz nazw zwierzcia"
          value={query}
          onChange={(e) => {
            setQuery(e.target.value);
            searchAnimals(e.target.value);
          }}
          style={{ width: "100%", padding: "8px" }}
        />

        {loading && <p>adowanie...</p>}
        {error && <p style={{ color: "red" }}>{error}</p>}

        {searchResults.length > 0 && (
          <ul
            style={{
              listStyle: "none",
              padding: 0,
              border: "1px solid #ccc",
              maxHeight: "200px",
              overflowY: "auto",
              background: "#fff",
              marginTop: 5,
              position: "absolute",
              width: 600,
              zIndex: 1000,
            }}
          >
            {searchResults.map((item) => (
              <li
                key={item.pageid}
                onClick={() => {
                  fetchAnimalDetails(item.title);
                  setSearchResults([]);
                  setQuery(item.title);
                }}
                style={{
                  padding: "8px",
                  cursor: "pointer",
                  borderBottom: "1px solid #eee",
                }}
              >
                {item.title}
              </li>
            ))}
          </ul>
        )}

        {selectedAnimal && (
          <div
            style={{
              marginTop: 20,
              border: "1px solid #ddd",
              padding: 15,
              maxWidth: 600,
              background: "#fafafa",
            }}
          >
            <h2>{selectedAnimal.title}</h2>
            {selectedAnimal.thumbnail && (
              <img
                src={selectedAnimal.thumbnail.source}
                alt={selectedAnimal.title}
                style={{ maxWidth: "100%", height: "auto" }}
              />
            )}
            <p>{selectedAnimal.extract}</p>
            <a
              href={selectedAnimal.content_urls?.desktop.page}
              target="_blank"
              rel="noopener noreferrer"
            >
              Czytaj wicej na Wikipedii
            </a>

            {user && (
              <button
                onClick={() => addFavorite(selectedAnimal.title)}
                style={{
                  marginTop: 15,
                  padding: "8px 12px",
                  cursor: "pointer",
                  backgroundColor: "#007acc",
                  color: "white",
                  border: "none",
                  borderRadius: 4,
                }}
              >
                Dodaj do ulubionych
              </button>
            )}

            {/* Opinie i oceny */}
            <div style={{ marginTop: 30 }}>
              <h3>Opinie</h3>
              {reviews.length === 0 && <p>Brak opinii</p>}
              <ul style={{ paddingLeft: 15 }}>
                {reviews.map((rev, idx) => (
                  <li key={idx} style={{ marginBottom: 10 }}>
                    <b>{rev.user}</b> ({new Date(rev.date).toLocaleString()}):
                    <p>{rev.text}</p>
                    <small>
                      Sodko: {rev.ratings.sladkosc} | Ciekawo: {rev.ratings.ciekawosc} | Grono:{" "}
                      {rev.ratings.groznosc}
                    </small>
                  </li>
                ))}
              </ul>

              {user && (
                <div style={{ marginTop: 15 }}>
                  <textarea
                    rows={3}
                    placeholder="Napisz opini..."
                    value={reviewText}
                    onChange={(e) => setReviewText(e.target.value)}
                    style={{ width: "100%", padding: 8 }}
                  ></textarea>

                  <div style={{ marginTop: 10 }}>
                    <label>
                      Sodko:
                      <input
                        type="range"
                        min={1}
                        max={5}
                        value={ratings.sladkosc}
                        onChange={(e) =>
                          setRatings((prev) => ({ ...prev, sladkosc: +e.target.value }))
                        }
                      />
                      {ratings.sladkosc}
                    </label>
                  </div>

                  <div style={{ marginTop: 10 }}>
                    <label>
                      Ciekawo:
                      <input
                        type="range"
                        min={1}
                        max={5}
                        value={ratings.ciekawosc}
                        onChange={(e) =>
                          setRatings((prev) => ({ ...prev, ciekawosc: +e.target.value }))
                        }
                      />
                      {ratings.ciekawosc}
                    </label>
                  </div>

                  <div style={{ marginTop: 10 }}>
                    <label>
                      Grono:
                      <input
                        type="range"
                        min={1}
                        max={5}
                        value={ratings.groznosc}
                        onChange={(e) =>
                          setRatings((prev) => ({ ...prev, groznosc: +e.target.value }))
                        }
                      />
                      {ratings.groznosc}
                    </label>
                  </div>

                  <button
                    onClick={addReview}
                    style={{
                      marginTop: 15,
                      padding: "8px 12px",
                      backgroundColor: "#007acc",
                      color: "white",
                      border: "none",
                      borderRadius: 4,
                      cursor: "pointer",
                    }}
                  >
                    Dodaj opini
                  </button>
                </div>
              )}
            </div>
          </div>
        )}

        {/* Modale */}
        {showAuthModal && (
          <AuthModal
            onClose={() => setShowAuthModal(false)}
            onLogin={(loggedUser) => {
              setUser(loggedUser);
              setShowAuthModal(false);
            }}
          />
        )}

        {showProfile && user && (
          <UserProfile
            user={user}
            onClose={() => setShowProfile(false)}
            onDeleteAccount={deleteAccount} 
          />
        )}
      </main>
    </div>
  );
}
