import React, { useState } from "react";
import quizData from "../data/quizData";
import "./Quizy.css";

export default function Quizy() {
  const [category, setCategory] = useState(null);
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [score, setScore] = useState(0);
  const [showScore, setShowScore] = useState(false);

  const startQuiz = (cat) => {
    setCategory(cat);
    setCurrentQuestion(0);
    setScore(0);
    setShowScore(false);
  };

  const handleAnswer = (answer) => {
    if (answer === quizData[category][currentQuestion].answer) {
      setScore(score + 1);
    }
    const nextQ = currentQuestion + 1;
    if (nextQ < quizData[category].length) {
      setCurrentQuestion(nextQ);
    } else {
      setShowScore(true);
    }
  };

  const backgroundStyle = {
    backgroundImage: `url(${process.env.PUBLIC_URL + '/las.jpg'})`,
    backgroundSize: "cover",
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
    minHeight: "100vh",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    padding: "20px",
  };

  const cardStyle = {
    backgroundColor: "rgba(0, 51, 25, 0.85)", 
    padding: "40px",
    borderRadius: "16px",
    maxWidth: "700px",
    width: "100%",
    textAlign: "center",
    color: "white",
    boxShadow: "0 4px 12px rgba(0,0,0,0.4)",
  };

  const questionObj = category && quizData[category][currentQuestion];

  return (
    <div style={backgroundStyle}>
      <div style={cardStyle}>
        {!category ? (
          <>
            <h1>Wybierz kategori quizu</h1>
            <div className="category-buttons">
              <button onClick={() => startQuiz("easy")}>atwy</button>
              <button onClick={() => startQuiz("medium")}>redni</button>
              <button onClick={() => startQuiz("hard")}>Trudny</button>
              <button onClick={() => startQuiz("quick")}>Szybkie Strzay</button>
            </div>
          </>
        ) : showScore ? (
          <>
            <h2>Twj wynik: {score} / {quizData[category].length}</h2>
            <button className="back-button" onClick={() => setCategory(null)}>
              Wr do wyboru kategorii
            </button>
          </>
        ) : (
          <>
            <h2>{questionObj.question}</h2>
            <div className="options">
              {questionObj.options.map((opt, i) => (
                <button key={i} onClick={() => handleAnswer(opt)}>
                  {opt}
                </button>
              ))}
            </div>
            <p className="progress">
              Pytanie {currentQuestion + 1} z {quizData[category].length}
            </p>
          </>
        )}
      </div>
    </div>
  );
}
